/* Pulser */
.pulser {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 15.625rem;
  height: 15.625rem;
  display: grid;
  place-content: center;
  border-radius: 50%;
  background: rgba(13, 14, 16, 0.05);
  backdrop-filter: blur(15px);
}
.pulser__content {
  position: relative;
  isolation: isolate;
  display: grid;
  place-content: center;
  width: 6.25rem;
  height: 6.25rem;
  background-image: linear-gradient(90deg, #885bfd 0%, #6328ff 68.76%);
  border-radius: 50%;
