  animation-delay: 0.75s;
  border: 1px solid var(--bs-primary);
  border-radius: 50%;
  z-index: -1;
}
.pulser__content::after {
  content: "";
  position: absolute;
  inset: 0;
  animation: multiplePulse 2s linear infinite;
  animation-delay: 1.5s;
  border: 1px solid var(--bs-primary);
  border-radius: 50%;
  z-index: -1;
