  background-image: linear-gradient(180deg, #040116 40.24%, rgba(4, 1, 22, 0) 100%), radial-gradient(117.36% 164.31% at -3.78% 50%, rgba(0, 0, 0, 0) 0%, #f65c34 81.3%, #fbbcac 100%);
}
.cta-8::after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;
  background-image: url(../img/shapes/cta-8-bg.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
  pointer-events: none;
}

/* Feature Section 1 */
.feature-section-1 {
  position: relative;
  z-index: 1;
  margin-top: -2rem;
  border-radius: 1.5rem 1.5rem 0 0;
  background: linear-gradient(180deg, var(--bs-light-bg-subtle) 0%, var(--bs-tertiary-bg) 100%);
}

/* Feature Section 2 */
.feature-section-2 {
  position: relative;
  isolation: isolate;
  background-image: url(../img/shapes/feature-section-2-grid.png);
  background-repeat: repeat;
  overflow: hidden;
}
.feature-section-2::after {
  content: "";
  position: absolute;
  inset: 0;
  z-index: -2;
  background: linear-gradient(90deg, var(--bs-tertiary-bg) 0%, var(--bs-tertiary-bg) 100%);
  filter: blur(100px);
}

/* Feature Section 3 */
.feature-section-3 {
  position: relative;
  isolation: isolate;
  overflow: hidden;
}
.feature-section-3::before {
  content: url(../img/shapes/feature-section-3-shape.png);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  pointer-events: none;
}

/* Feature 3 */
.feature-3 {
  position: relative;
  isolation: isolate;
  overflow: hidden;
  background-image: url(../img/feature-bg-1.png);
  background-size: contain;
  background-repeat: no-repeat;
}
.feature-3::after {
  content: "";
  position: absolute;
  inset-inline: 0;
  inset-block-start: 25%;
  inset-block-end: 0;
  background: var(--bs-body-bg);
  filter: blur(50px);
  z-index: -1;
}

/* Feature 4 */
.feature-4 {
  background-image: url(../img/shapes/hero-2-bg-shape.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  isolation: isolate;
}
.feature-4::before {
  content: "";
  position: absolute;
  top: 0;
  inset-inline: 0;
  height: 12.5rem;
  background-color: var(--bs-body-bg);
  filter: blur(100px);
  z-index: -1;
  pointer-events: none;
}
.feature-4::after {
  content: "";
  position: absolute;
  bottom: 0;
  inset-inline: 0;
  height: 12.5rem;
  background-color: var(--bs-body-bg);
  filter: blur(100px);
  z-index: -1;
  pointer-events: none;
}
@media (min-width: 992px) {
  .feature-4__title {
    font-size: 1.25rem;
  }
}

[data-bs-theme=dark] .feature-4 {
  background-image: url(../img/shapes/hero-2-bg-shape-dark.png);
}

/* Footer 1 */
.footer-1 {
  background-image: url(../img/shapes/footer-1-grid.png);
  background-repeat: repeat;
}
.footer-1::after {
  content: "";
  position: absolute;
  inset: 0;
  background-color: var(--bs-dark);
  z-index: -2;
  filter: blur(150px);
}

/* Footer 2 */
.footer-2 {
  border-radius: 3rem 3rem 0 0;
}
.footer-2__container {
  border-radius: 3rem 3rem 0 0;
  backdrop-filter: blur(3px);
}

/* Footer 3 */
.footer-3 {
  position: relative;
  z-index: 1;
}
.footer-3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 40%;
  height: 300px;
  background-image: url(../img/shapes/footer-3-line.png);
  background-position: left center;
  background-size: contain;
  background-repeat: no-repeat;
  pointer-events: none;
}
.footer-3::after {
  content: "";
  position: absolute;
  inset: 0;
  -webkit-mask-image: url(../img/shapes/footer-3-shape.png);
  mask-image: url(../img/shapes/footer-3-shape.png);
  mask-size: cover;
  mask-position: center top;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  background-color: var(--bs-dark);
  z-index: -1;
  pointer-events: none;
}

.generate-image-prompt-section {
  background-image: linear-gradient(transparent, rgba(var(--bs-light-rgb), 0.025));
}
.generate-image-prompt-section::after {
  content: "";
  position: absolute;
  inset: 0;
  z-index: -1;
  pointer-events: none;
  background-color: rgba(var(--bs-black-rgb), 0.25);
  filter: blur(50px);
}

.generate-image-prompt {
  padding-block: clamp(12rem, 14vw + 0.5rem, 18rem);
  background-image: url(../img/generate-img-input-bg.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.generate-image-prompt__form {
  display: flex;
  padding: 0.7rem 1rem;
  border-radius: 0.75rem;
  border: 1px solid rgba(193, 193, 193, 0.36);
  background: rgba(188, 188, 192, 0.2);
  backdrop-filter: blur(20px);
}
.generate-image-prompt__input {
  background-color: transparent;
  border: 1px solid transparent;
  color: var(--bs-light);
}
.generate-image-prompt__input:focus {
  background-color: transparent;
  border-color: transparent !important;
  color: var(--bs-light);
}
.generate-image-prompt__input::placeholder {
  color: rgba(var(--bs-light-rgb), 0.75);
}

