/* Testimonial Section 1 */
.testimonial-section-1 {
  position: relative;
  background: linear-gradient(rgba(0, 0, 0, 0.3) 0%, transparent 21%, transparent 78%, rgba(0, 0, 0, 0.1) 100%);
  overflow: hidden;
}
@media (min-width: 1400px) {
  .testimonial-section-1::after {
    content: "";
    position: absolute;
    inset: 0;
    pointer-events: none;
    background: linear-gradient(270deg, rgba(0, 0, 0, 0.3) 0%, transparent 21%, transparent 78%, rgba(0, 0, 0, 0.1) 100%);
    z-index: 1;
  }
}
.testimonial-section-1__inner {
  position: relative;
  z-index: 1;
}
.testimonial-section-1__inner::after {
  content: "";
  position: absolute;
  inset: 0;
  pointer-events: none;
  background: linear-gradient(270deg, rgba(0, 0, 0, 0.3) 0%, transparent 21%, transparent 78%, rgba(0, 0, 0, 0.1) 100%);
  z-index: 1;
}
@media (min-width: 1400px) {
  .testimonial-section-1__inner::after {
    display: none;
  }
}
.testimonial-section-1__shape {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  overflow: unset;
}
.testimonial-section-1__path {
  animation: animateFill 25s infinite linear alternate;
}

/* Testimonial Section 2 */
.testimonial-section-2 {
  max-height: 43.75rem;
  mask: linear-gradient(transparent, white 20%, white 80%, transparent);
  overflow: hidden;
}

/* Testimonial Section 3 */
@media (min-width: 992px) {
  .testimonial-section-3::before {
    content: "";
    width: 100%;
    height: 18.75rem;
    max-width: 50rem;
    background: linear-gradient(180deg, #5383ff 0%, #5135ff 100%);
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -2;
    pointer-events: none;
    filter: blur(150px);
  }
}

/* Testimonial Section 4 */
.testimonial-section-4 {
  position: relative;
  isolation: isolate;
  overflow: hidden;
}
.testimonial-section-4::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 81.25rem;
  height: 50%;
  background: linear-gradient(180deg, rgba(126, 77, 254, 0.2) 0%, rgba(99, 40, 255, 0.2) 100%);
  filter: blur(100px);
  z-index: -1;
  opacity: 0.35;
}
.testimonial-section-4__circle {
  position: absolute;
  top: 0;
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  background-image: linear-gradient(rgba(var(--bs-primary-rgb), 0.35), var(--bs-dark), var(--bs-dark), rgba(var(--bs-primary-rgb), 0.35));
  animation: rotate360 12s linear infinite forwards;
}
.testimonial-section-4__circle::before {
  content: "";
  position: absolute;
  inset: 3px;
  background-color: var(--dark-saas);
  border-radius: 50%;
}

