/* Animation Section */
@keyframes pulse {
  100% {
    transform: scale(2);
    opacity: 0;
  }
}
@keyframes multiplePulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(2.5);
    opacity: 0;
  }
}
@keyframes imageBeat {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.7);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes scroll {
  to {
    transform: translate(calc(-50% - 0.5rem));
  }
}
@keyframes scrollY {
  to {
    transform: translateY(calc(-50% - 0.5rem));
  }
}
@keyframes animateFill {
  0% {
    fill: var(--bs-primary);
    fill-opacity: 0.35;
  }
  25% {
    fill: var(--bs-danger);
    fill-opacity: 0.5;
  }
  50% {
    fill: var(--bs-success);
    fill-opacity: 0.7;
  }
  100% {
    fill: var(--bs-warning);
    fill-opacity: 0.5;
  }
}
@keyframes animateBackgroundColor {
  0% {
    background-color: rgba(var(--bs-primary-rgb), 0.25);
  }
  25% {
    background-color: rgba(var(--bs-success-rgb), 0.25);
  }
  50% {
    background-color: rgba(var(--bs-info-rgb), 0.25);
  }
  75% {
    background-color: rgba(var(--bs-warnign-rgb), 0.25);
  }
  100% {
    background-color: rgba(var(--bs-danger-rgb), 0.25);
  }
}
@keyframes upDown {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(5%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes rotate360 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes hidePreloader {
  0%, 100% {
    opacity: 0.8;
  }
  50% {
    opacity: 1;
  }
}
