/* Line Clamp */
.line-clamp {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	overflow: hidden;
	&--1 {
		-webkit-line-clamp: 1;
	}
	&--2 {
		-webkit-line-clamp: 2;
	}
	&--3 {
		-webkit-line-clamp: 3;
	}
	&--4 {
		-webkit-line-clamp: 4;
	}
	&--5 {
		-webkit-line-clamp: 5;
	}
}
