/* Portfolio Card */
.portfolio-card {
	&:hover {
		.portfolio-card__img {
			transform: scale(1.1) rotate(3deg);
		}
		.portfolio-card__content {
			opacity: 1;
			visibility: visible;
			pointer-events: unset;
		}
	}
	&__content {
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
	}
}
